import axios from 'axios'
import {
    createNamespacedHelpers
} from 'vuex'
const LoginStore = createNamespacedHelpers('LoginStore')
export default {
    methods: {
        getData() {
            axios.get(`/api/member/getMemberInfo/${this.getLogin.user.id}`)
                .then(res => {
                    this.id = this.getLogin.user.id;
                    this.name = res.data.name;
                    this.zipcode = res.data.zipcode;
                    this.addr1 = res.data.addr1;
                    this.addr2 = res.data.addr2;
                    this.tel = res.data.tel;
                    this.email = res.data.email;
                })
        },
        update() {
            let member;
            if (this.pwd1 != null)
                if (this.pwd1 == this.pwd2) {
                    member = {
                        id: this.id,
                        password: this.pwd1,
                        name: this.name,
                        zipcode: this.zipcode,
                        addr1: this.addr1,
                        addr2: this.addr2,
                        tel: this.tel,
                        email: this.email,
                    }
                }
            else {
                member = {
                    id: this.id,
                    name: this.name,
                    zipcode: this.zipcode,
                    addr1: this.addr1,
                    addr2: this.addr2,
                    tel: this.tel,
                    email: this.email,
                }
            }
            console.log(member)
            axios.put('/api/member/updateMember', member)
                .then(() => {
                    alert(" ")
                    this.getData();
                })
                .catch(() => alert(" "))
        },
        secession() {
            axios.get('/api/member/login', {
                    params: {
                        id: this.id,
                        password: this.password,
                    }
                })
                .then(() => {
                    axios.delete(`/api/member/delete/${this.id}`)
                        .then(() => {
                            alert(" .");
                            this.$router.push('/')
                        })
                        .catch(() => {
                            alert("    .");
                        })
                })
                .catch(() => {
                    alert("  .")

                })
                .finally(this.$refs.password.reset())
        },
        execDaumPostcode() {
            new window.daum.Postcode({
                oncomplete: (data) => {
                    if (this.extraAddress !== "") {
                        this.addr2 = "";
                    }
                    if (data.userSelectedType === "R") {
                        //     
                        this.addr1 = data.roadAddress;
                    } else {
                        //     (J)
                        this.addr1 = data.jibunAddress;
                    }
                    // //       .
                    // if (data.userSelectedType === "R") {
                    //     //    . ( )
                    //     //     "//" .
                    //     if (data.bname !== "" && /[||]$/g.test(data.bname)) {
                    //         // this.addr2 += data.bname;
                    //     }
                    //     //  ,   .
                    //     if (data.buildingName !== "" && data.apartment === "Y") {
                    //         this.addr2 +=
                    //             this.addr2 !== "" ?
                    //             `, ${data.buildingName}` :
                    //             data.buildingName;
                    //     }
                    //     //    ,     .
                    //     if (this.addr2 !== "") {
                    //         this.addr2 = `(${this.addr2})`;
                    //     }
                    // } else {
                    //     this.addr2 = "";
                    // }
                    //  .
                    this.zipcode = data.zonecode;
                },
            }).open();
        }
    },
    components: {},
    computed: {
        ...LoginStore.mapGetters(['getLogin']),
    },
    mounted() {
        console.log(this.getLogin)
        if (this.getLogin) {
            this.getData();
        }
    },
    data() {
        return {
            dialog: false,
            id: '',
            pwd1: '',
            pwd2: '',
            name: '',
            zipcode: '',
            addr1: '',
            addr2: '',
            tel: '',
            email: '',
            password: '',
        }
    }
}
